<template>
  <div :class="{'search-open-contact':!$store.state.headerStatus}">
    <!-- <header id="wx-header">
      <div class="center">
        <router-link to="/warehousing" tag="div" class="iconfont icon-return-arrow">
          <span>返回</span>
        </router-link>
        <span>产品产量上报</span>
      </div>
    </header> -->
  <form>
  <van-cell-group>
    <van-cell title="库区" icon="shop-o" is-link arrow-direction="down" value="请选择" >
      <van-dropdown-menu>
        <van-dropdown-item v-model="area" :options="areaList" />
      </van-dropdown-menu>
    </van-cell>
    <van-cell title="库位" icon="shop-o" is-link arrow-direction="down" value="请选择" >
      <van-dropdown-menu>
        <van-dropdown-item v-model="location" :options="locationList" />
      </van-dropdown-menu>
    </van-cell>
    <van-cell title="产品" icon="shop-o" is-link arrow-direction="down" value="请选择" >
      <van-dropdown-menu>
        <van-dropdown-item v-model="product" :options="productList" />
      </van-dropdown-menu>
    </van-cell>
    <van-field
      placeholder="请输入"
      label="产量"
      left-icon="shop-o"
      v-model="output"
    />
  </van-cell-group>

  <van-button type="primary" size="large" @click="submit">提交产量</van-button>
  </form>
  </div>
</template>

<script>
  import { Dialog,Toast } from 'vant';
export default {
  name: 'hello',
  data () {
    return {
      area: null,
      location: null,
      product:null,
      output: '',
      areaList: [
        { text: '1号库区', value: '1号库区' },
        { text: '2号库区', value: '2号库区' },
        { text: '3号库区', value: '3号库区' }
      ],
      locationList: [
        { text: '1号库位', value: '1号库位' },
        { text: '2号库位', value: '2号库位' },
        { text: '3号库位', value: '3号库位' },
      ],
      productList: [
        { text: '工业盐', value: '工业盐' },
        { text: '加碘食盐', value: '加碘食盐' },
        { text: '泡菜盐', value: '泡菜盐' },
        { text: '雪花盐', value: '雪花盐' },
      ]
    }
  },
  methods: {
    submit: function(){
      if(this.area==null || this.area.length==0){
        Dialog.alert({title: '提交产量失败', message: '库区不能为空'});
        return ;
      }
      else if(this.location==null || this.location.length==0){
        Dialog.alert({title: '提交产量失败', message: '库位不能为空'});
        return ;
      }
      else if(this.product==null || this.product.length==0){
        Dialog.alert({title: '提交产量失败', message: '产品不能为空'});
        return ;
      }
      else if(this.output==null || this.output.length==0){
        Dialog.alert({title: '提交产量失败', message: '产量不能为空'});
        return ;
      }
      this.$http.post('/api/warehousing/add',
        'area='+this.area+'&location='+this.location+'&product='+this.product+'&output='+this.output
      ).then((response) => {
        let result = response.data;
        if (!result || result.code != 200) {
          Dialog.alert({title: '提交产量失败', message: result.message});
          return;
        } else {
          let  ctx = this;
          Toast({type:'success',message: result.message, icon: 'success', duration: 500,onClose:function(){
              ctx.$router.push({ path: '/warehousing' });
            }});
        }
      });
    }
  }
}
</script>

<style>
   .van-field__control{
       text-align: right;
   }
</style>



